export default {
	name: 'basics',
	data() {
		return {
			selectedCarObj:{},
			orderDetail: '',
			hasLogin: false,
			animationSelect: '',
			currentSelectId: 0,
			carWashDesc: '',
			loading: false,
			currentSelectAddress: {},
			currentSelectCar: '',
			modalName: null,
			showQiPao: false,
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			currentSwiper: 0,
			indicatorDots: false,
			autoplay: false,
			interval: 2000,
			duration: 500,
			tarHeight: 50,
			isRight: true,
			screenHeight: 0,
			mapHeight: 500,
			contentheght: 110,
			title: 'map',
			latitude: 39.909,
			longitude: 116.39742,
			covers: [{
				id: 'dddds',
				latitude: 39.909,
				longitude: 116.39742,
				iconPath: '../../static/location.png',
				callout: {
					content: '无法服务，附近暂无技师',
					bgColor: '#d81e06',
					borderColor: '#d81e06',
					borderRadius: 50,
					display: 'ALWAYS',
					padding: 10,
					fontSize: 15,
					color: '#fff'
				}
			}],
			myCarList: [],
			selectCarList: []
		};
	},
	methods: {
		//获取产品信息
		getProducts() {
			let _this = this;
			this.$api.request({}, '/api/product/productList', 'GET').then(res => {
				_this.selectCarList = res.data.data
			})
		},
		//获取全部车辆
		getAllCars() {
			let _this = this;
			_this.myCarList = []
			//获取全部车辆
			this.$api.request({}, 'api/vehicle/vehicleList', 'GET').then(res => {
				let carLists = res.data.data;
				let carItem = [];
				for (let i = 0; i < carLists.length; i++) {
					if(i == 0){
						_this.selectedCarObj = carLists[0];
					}
					carItem.push(carLists[i]);
					if ((i + 1) % 3 == 0) {
						_this.myCarList.push(carItem);
						carItem = []
					} else if ((i + 1) == carLists.length) {
						_this.myCarList.push(carItem);
					}
				}
				console.log("车辆：", _this.myCarList)
			})
		},
		selectPromotion(id, index) {
			console.log("id：" + id, "下标：" + index);
			let that = this;
			this.carWashDesc = this.selectCarList[index].remarks;
			this.currentSelectId = id;
			this.loading = false;
			setTimeout(function() {
				that.loading = true;
			}, 100);

			this.animationSelect = 'animation-shake';
			setTimeout(() => {
				that.animationSelect = 'shadow bg-white solids';
			}, 100);
		},
		hideModal(e) {
			this.modalName = null;
		},
		doCreateOrder(car) {
			this.selectedCarObj = car;
			this.currentSelectCar = car.number;
			this.modalName = 'ChooseModal';
			this.selectPromotion(1, 0);
		},
		//跳转添加车辆页面
		toPages(url) {
			if (!this.hasLogin) {
				url = '/pages/user/login'
			}
			uni.navigateTo({
				url: url
			});
		},
		//上一页
		preSwiper() {
			this.currentSwiper--;
		},
		//下一页
		nextSwiper() {
			this.currentSwiper++;
		},
		// 监听swiper 改变
		swiperChange(e) {
			this.currentSwiper = e.detail.current
		},
		//选择车辆地址
		doSelectLocations() {
			let _this = this;
			uni.chooseLocation({
				success: function(res) {
					console.log('位置名称：' + res.name);
					console.log('详细地址：' + res.address);
					console.log('纬度：' + res.latitude);
					console.log('经度：' + res.longitude);
					_this.currentSelectAddress = res;
				}
			});
		},
		//选择车辆
		doSelectCars() {
			this.isRight = !this.isRight;
			if (this.isRight) {
				this.contentheght = 110;
			} else {
				this.contentheght = 220;
			}
			this.mapHeight = this.screenHeight - this.contentheght;
		}
	},
	mounted() {
		this.screenHeight = uni.getStorageSync('screenHeight') - 50 - uni.getStorageSync('bottomTarHeight');
		this.mapHeight = this.screenHeight - this.contentheght;
		//是否登录判断
		this.hasLogin = uni.getStorageSync('hasLogin');
		console.log("是否登录:", this.hasLogin)
		if (!this.hasLogin) {
			uni.navigateTo({
				url: '/pages/user/login'
			})
		}
		//加载车辆
		this.getAllCars();
		//加载产品
		this.getProducts();
	}
};
